var UNLOCK_ROUND_DURATION = 60 * 60 * 24;
var MAX_LOCK_ROUNDS = 1460;
var MAX_LOCK_DURATION = MAX_LOCK_ROUNDS * UNLOCK_ROUND_DURATION;
var MIN_INITIAL_LOCK_AMOUNT = 1e10;
var MIN_TOP_UP_AMOUNT = 1e9;

